body {
  background: #0e1021;
  color: #b0b0b0;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  padding: 10vh 10vw;
  height: 100vh;
  weight: 100vw;
  margin: 0;
  letter-spacing: 0.2px

}

.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;

}

//App header and input

.App-header {
  display: flex;
  justify-content: left;
  flex-direction: column;
  width: 60%;
  padding-bottom: 20px;
  @media (max-width: 768px) {
    width: 80%;
  }
  @media (max-width: 580px) {
    width: 100%;
  }

  .errorMessage {
    color: #da5656;
  }

  .siteDescription {
    margin-top: 10vh;
    text-align: center;
    font-size: 22px;
    font-weight: 500;
    color: rgba(145, 145, 145, 0.75);
  }
}

.inputForm {
  width: 100%;
  //max-width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;

  input {
    width: 70%;
    height: 32px;
    border: 2px solid white;
    border-radius: 5px;
    margin-right: 1em;
    padding: 2px 20px;
    font-size: 16px;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
    outline: none;
    @media (max-width: 425px) {
      margin-right: 0.6em;
    }

    &:hover {
      border-color: #ffcb47;
    }

    &:focus {
      border-color: #ffcb47;
      box-shadow: 0 0 8px rgba(245, 217, 141, 0.75);
    }
  }

  button {
    width: 20%;
    height: 39px;
    background-color: #fdca43;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: #0e1021;
    transition: background-color 0.2s ease;

    &:hover {
      background-color: #fcb810;
    }
  }
}

.localStorageButtons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 20px;

  .localStorageButton {
    background: none;
    border: none;
    color: #cecece;
    cursor: pointer;
    padding: 0;
    font-size: 16px;
    margin-right: 12px;

    &:hover {
      color: #ffcb47;
      text-decoration: underline;
    }
  }
}

.errorMessage {
  width: 100%;
  max-width: 500px;
}


// weather right now

.weatherRightNow {
  width: 60%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding: 40px 0 40px 0;
  @media (max-width: 768px) {
    width: 100%;
  }
  @media (max-width: 425px) {
    flex-wrap: wrap;
  }

  > div:not(:last-child) {
    margin-right: 4vw;
    @media (max-width: 425px) {
      margin-bottom: 50px;
    }
  }
}

.weatherInfo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  .cityName {
    font-size: 24px;
    font-weight: bold;
    color: #cecece;
    letter-spacing: 0.7px;
  }
}

.weatherRightNowIcon {
  position: relative;
}

.weatherRightNowIcon p {
  position: absolute;
  top: 60%;
  left: 4%;
  text-align: center;
}

.weatherTemperature {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 54px;
}

.weatherDescription {
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 18px;

  > div:not(:last-child) {
    margin-bottom: 5px;
  }
}

// forecast weather

.forecastList {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 60px 0 60px 0;

  .forecastListItem {
    list-style-type: none;
    margin-bottom: 25px;
    @media (max-width: 425px) {
      padding: 0;
    }

    .forecastListDateItem {
      font-size: 18px;
      font-weight: 600;
      letter-spacing: 0.7px;
      color: #cecece;
      padding-bottom: 10px;
    }

    ul {
      margin: 0;
      padding: 0;

      li {
        display: flex;
        flex-direction: row;
        align-items: center;
      }
    }

  }
}

.weatherIcon {
  width: 50px;
}
